.order {
  display: flex;
  justify-content: flex-end;
  padding-bottom: 15px;
}

.order__container {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  padding-top: 112px;
  width: 100%;
}

.order__body {
  padding-left: 16.5vw;
}

.order__title {
  max-width: 550px;
  font-weight: 500;
  font-size: 50px;
  line-height: 60px;
  color: var(--color-black);
}

.order__dsr {
  margin-top: 15px;
  max-width: 900px;
  font-weight: 400;
  font-size: 32px;
  line-height: 40px;
  color: var(--color-black);
}

.form {
  padding-top: 42px;
  padding-bottom: 70px;
  max-width: 760px;
}

.form__item {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-bottom: 20px;
}

.form__item:first-child {
  margin-bottom: 15px;
}

.form__item:nth-child(3) {
  margin-bottom: 11px;
}

.form__btn {
  padding: 0;
  background-color: transparent;
  border: none;
  margin-top: 10px;
  width: 288px;
  height: 64px;
  color: var(--color-white);
  background-color: var(--color-blue);
  transition: background-color 0.3s ease-in-out;
}

.form__btn:hover {
  background-color: var(--color-red);
  transition: background-color 0.3s ease-in-out;
}

.form__consent {
  margin-top: 10px;
  width: 350px;
  font-weight: 400;
  font-size: 20px;
  line-height: 40px;
  color: var(--color-black);
}

.form-title {
  font-weight: 400;
  font-size: 32px;
  color: var(--color-gray);
}

.form-input {
  border: 1px solid var(--color-gray);
  width: 600px;
  height: 36px;
}

.textarea {
  padding-bottom: 12px;
  overflow: hidden;
  overflow-wrap: break-word;
  border: 1px solid var(--color-gray);
  width: 600px;
  height: 90px;
  resize: none;
}

.project__items {
  display: flex;
  justify-content: flex-start;
  padding-bottom: 8px;
  width: 600px;
}

.project__label:not(:last-child) {
  margin-right: 12px;
}

.project__input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.project > span {
  display: flex;
  align-items: center;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.project__item::before {
  content: "";
  display: inline-block;
  border: 1px solid var(--color-gray);
  border-radius: 20px;
  padding: 7px 18px;
  font-weight: 400;
  font-size: 20px;
  text-align: center;
  color: var(--color-gray);
  cursor: pointer;
}

.project__item--logo::before {
  content: "логотип";
}

.project__item--identity::before {
  content: "айдентика";
}

.project__item--landing::before {
  content: "лендинг";
}

.project__item--other::before {
  content: "другое";
}

.project__label > input:checked + span::before {
  border-color: var(--color-blue);
  box-shadow: 0 0 0 2px var(--color-blue);
  color: var(--color-blue);
}

.task__title {
  max-width: 120px;
  line-height: 40px;
}

.budget {
  display: flex;
  justify-content: flex-start;
}

.budget__title {
  width: 160px;
}

.budget__items {
  display: flex;
  justify-content: space-between;
  padding-bottom: 8px;
  width: 465px;
}

.budget__label:not(:last-child) {
  margin-right: 12px;
}

.budget__input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.budget__input > span {
  display: flex;
  align-items: center;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.budget__item::before {
  content: "";
  display: inline-block;
  border: 1px solid var(--color-gray);
