.path-animation {
  animation: dash 15s infinite;
  animation-fill-mode: forwards;
}

.circle-animation {
  animation: circle 15s infinite;
}

@keyframes circle {
  from {
    transform: scale(1);
  }
  to {
    transform: translate(-50%, -50%) scale(2);
  }
}

@keyframes dash {
  from {
    stroke-dashoffset: 15000;
    transform: scale(1);
  }
  to {
    stroke-dashoffset: 0;
    transform: translate(-50%, -50%) scale(2);
  }
}

